body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.home-top {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    grid-gap: 23%;
    gap: 23%;
    margin-top: 10px;
}

.pagination-flex {
    display: flex;
    flex-direction: row;
    grid-gap: 50px;
    gap: 50px;
    align-items: center;
}

.movie-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    align-items: center;
    justify-items: center;
}

li {
    list-style: none;
}

select {
    width: 200px;
    margin-right: 30px;
    background-color: mediumpurple;
    color: white;
    font-weight: bold;
    font-size: medium;
}

.btn-outline-dark:hover {
    background-color: mediumpurple !important;
}

.movie-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px;
    width: 300px;
    height: 500px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    margin: 20px;
}

img {
    width: 300px;
    height: 420px;
}

.movie-title:hover {
    background-color: lightgray;
}

span {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.card-score-fav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.character-limit {
    max-width: 250px; /* Adjust the max width as per your needs */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0px 20px 0px 20px;
}

.loading-spinner {
    margin: auto;
    width: 50px;
    height: 50px;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    -webkit-animation: spin 1s ease-in-out infinite;
            animation: spin 1s ease-in-out infinite;
}
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

h1 {
    text-align: center;
    padding-top: 30px;
}

.fav-movie-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    align-items: center;
    justify-items: center;
}

.fav-pagination {
    display: flex;
    justify-content: center; /* Horizontally center */
    margin-top: 10px;
}

.login-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    grid-gap: 20px;
    gap: 20px;
}

.input-box {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.login-box input {
    width: 400px;
    height: 30px;
    border-top: none;
    border-left: none;
    border-right: none;
}

.login-box input:focus {
    background-color: #e1f6ff;
}

.login-box label {
    color: gray;
}

h1 {
    text-align: center;
    padding-top: 30px;
}

.rated-movie-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    text-align: center;
    align-items: center;
    justify-items: center;
}

.rated-pagination {
    display: flex;
    justify-content: center; /* Horizontally center */
    margin-top: 10px;
}

.detail-container {
    display: flex;
    flex-direction: row;
    grid-gap: 30px;
    gap: 30px;
    justify-content: center;
    margin-top: 50px;
    margin: 30px 100px 10px 100px;
}

.detail-container img {
    height: 80vh;
    width: 400px;
}

.genres-flex {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
}

h1 {
    margin: 0px;
}
p {
    margin-top: 5px;
    margin-bottom: 10px;
}

.genres-flex p {
    border: 1px solid royalblue;
    border-radius: 30px;
    padding: 5px;
    background-color: royalblue;
    color: white;
    font: 0.8rem sans-serif;
}
.rating-box {
    height: 30px;
}

.rating-flex {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
}

.rating-flex Button {
    height: 30px;
}

.production-flex {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    text-align: center;
}

.production-flex img {
    width: 60px;
    height: 40px;
}
.production-flex p {
    font-size: 15px;
}

.btn-outline-dark {
    font-size: 15px;
    font-weight: bold;
}

.custom {
    width: 100px !important;
    height: 45px !important;
}

.logo {
    height: 80px;
    width: 120px;
}

.header-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: mediumpurple;
}

.username {
    padding-right: 30px;
    color: white;
    font-weight: bold;
    font-size: 30px;
}

.login {
    text-decoration: none;
}

